<template>
  <router-view v-if="this.$route.name !== 'SupplierList'" />
  <div class="app-container" v-else>
    <div class="filter-container">
      <el-input
        v-model="listQuery.name"
        placeholder="名称"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >
        搜索
      </el-button>
      <el-button
        class="filter-item"
        style="margin-left: 10px;"
        type="primary"
        icon="el-icon-edit"
        @click="handleCreate"
      >
        添加
      </el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      border
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="ID" width="75">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column align="center" label="ID" width="75">
        <template slot-scope="scope">
          {{ scope.row.id }}
        </template>
      </el-table-column>
      <el-table-column label="名称" align="center">
        <template slot-scope="scope">
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label="URL">
        <template slot-scope="scope">
          {{ scope.row.url }}
        </template>
      </el-table-column>
      <el-table-column class-name="status-col" label="状态" align="center">
        <template slot-scope="scope">
          <span>{{ statusMap[scope.row.status] }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{ row }">
          <el-button
            type="primary"
            size="mini"
            @click="handleUpdate(row)"
            icon="el-icon-edit"
          >
            编辑
          </el-button>
          <el-button size="mini" @click="handleModifyStatus(row)">
            {{ 0 === row.status ? "启用" : "停用" }}
          </el-button>
          <router-link
            :to="{ name: 'Channel', params: { supplierId: row.id } }"
          >
            <el-button size="mini">
              通道配置
            </el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="supplier"
        label-position="left"
      >
        <el-form-item label="名称" prop="name">
          <el-input v-model="supplier.name" />
        </el-form-item>
        <el-form-item label="URL" prop="url">
          <el-input v-model="supplier.url" />
        </el-form-item>
        <el-form-item label="备注">
          <el-input
            v-model="supplier.description"
            :autosize="{ minRows: 2, maxRows: 4 }"
            type="textarea"
            placeholder="请输入"
          />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">
          取消
        </el-button>
        <el-button
          type="primary"
          @click="dialogStatus === 'create' ? createData() : updateData()"
        >
          保存
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { list, update, create } from "@/api/supplier";
import { validURL } from "@/utils/validate";
import Pagination from "@/components/Pagination";
import waves from "@/directive/waves"; // waves directive

export default {
  components: { Pagination },
  directives: { waves },
  data() {
    const checkURL = (rule, value, callback) => {
      if (value) {
        if (!validURL(value)) {
          callback(new Error("请输入正确的地址"));
        } else {
          callback();
        }
      } else {
        callback(new Error("请输入地址"));
      }
    };
    return {
      total: 0,
      codeOptions: [],
      listQuery: {
        name: "",
        pageNo: 1,
        pageSize: 20
      },
      supplier: {
        id: undefined,
        name: "",
        url: "",
        description: "",
        status: 1
      },
      list: null,
      listLoading: true,
      rules: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
        url: [
          {
            required: true,
            message: "请输入正确的地址",
            trigger: "blur",
            validator: checkURL
          }
        ]
      },
      statusMap: {
        1: "正常",
        0: "停用"
      },
      textMap: {
        update: "编辑",
        create: "创建"
      },
      dialogFormVisible: false,
      dialogStatus: ""
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      list(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
        }
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.fetchData();
    },
    resetTemp() {
      this.supplier = {
        id: undefined,
        name: "",
        description: "",
        url: "",
        status: "1"
      };
    },
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    handleUpdate(row) {
      this.supplier.id = row.id;
      this.supplier.name = row.name;
      this.supplier.status = row.status;
      this.supplier.description = row.description;
      this.supplier.url = row.url;
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
    },
    handleModifyStatus(row) {
      let data = {
        id: row.id,
        status: row.status === 0 ? 1 : 0
      };
      update(data).then(response => {
        if (response.code === 0) {
          row.status = data.status;
          this.$message({
            message: "操作成功",
            type: "success"
          });
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
        }
      });
    },
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          create(this.supplier).then(response => {
            if (response.code === 0) {
              this.fetchData();
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "创建成功",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
          });
        }
      });
    },
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          update(this.supplier).then(response => {
            if (response.code === 0) {
              this.fetchData();
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "更新成功",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
          });
        }
      });
    }
  }
};
</script>
